import * as angular from "angular";
export function createModule(options) {
    var name = options.name, deps = options.deps, components = options.components, services = options.services;
    var app = angular.module(name, deps || []);
    (components || []).forEach(function (c) { return app.component(c.componentName, c); });
    (services || []).forEach(function (s) { return app.service(s.serviceName, s); });
    return app;
}
