import { HeroService } from "../hero.service";
var HeroListController = /** @class */ (function () {
    function HeroListController(heroService) {
        this.heroService = heroService;
    }
    HeroListController.prototype.$routerOnActivate = function () {
        var _this = this;
        this
            .heroService
            .getHeroes()
            .subscribe(function (data) {
            _this.heroes = data;
        });
    };
    HeroListController.prototype.isSelected = function (id) {
        return true;
    };
    HeroListController.$inject = [HeroService.serviceName];
    return HeroListController;
}());
var template = require('./herolist.component.html');
export var HeroListComponent = {
    controller: HeroListController,
    template: template,
    componentName: 'clHeroList'
};
