import { createModule } from "./utils";
import { HeroesModule } from "./heroes/heroes.module";
import { AppComponent } from "./app.component";
var COMPONENTS = [
    AppComponent
];
export var appModule = createModule({
    name: 'clApp',
    deps: [
        'ngComponentRouter',
        HeroesModule,
    ],
    components: COMPONENTS
})
    .config(['$locationProvider',
    function ($locationProvider) { return $locationProvider.html5Mode(false); }
])
    .value('$routerRootComponent', AppComponent.componentName);
